header{
  background: #000;
  color: #fff;
  border: 2px solid #000;
}

header > a{
  display: inline-block;
  padding: 10px 20px;
  margin: 5px;
  transition: .5s;
}

header > a:hover{
  background: #fff;
  color: #000;
  display: inline-block;
  padding: 10px 20px;
}

header > a.active{
  background: #fff;
  color: #000;
  display: inline-block;
  padding: 10px 20px;
}

main > div{
  border: 2px solid #000;
  width: 100%;
  height: 250px;
  text-align: center;
  padding-top: 180px;
}

main .one{
  background: #eee;
}
main .two{
  background: #282c34;
  color: #fff;
}


.App {
  text-align: center;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
